/** ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïêüè≥‚Äçüåà ÂÆûÁî®ÂäüËÉΩ üè≥‚Äçüåà‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê
 *  Âà©Áî® composition-api ÂÆûÁé∞ÁöÑ‰∏Ä‰∫õÂÆûÁî®ÂäüËÉΩ
 ** ‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïêüöß ÂÆûÁî®ÂäüËÉΩ üöß‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê‚ïê */
import { customRef, nextTick, watch, watchEffect, WatchSource } from "vue";

class PromiseObj<T, Err = Error> {
  pending = false;
  fulfilled = false;
  rejected = false;
  data = {} as T;
  error = {} as Err;
  private p = Promise.resolve() as Promise<unknown>;
  setP(p: Promise<unknown>) {
    this.p = p;
  }
  equalP(p: Promise<unknown>) {
    return this.p === p;
  }
  reLoad() {}
}

interface usePromiseComputedOptions<T> {
  /** ÂáΩÊï∞ÂÜÖÁöÑ‰æùËµñÂèòÊõ¥ÁöÑÊó∂ÂÄôÂ∞±ÈáçÊñ∞ËÆ°ÁÆó,ÊåáÂÆö‰∫Ü deps Âàô‰∏ç‰ºö‰æùËµñ‰∫é getter ÂÜÖÁöÑ ref ‰∫Ü
   *  tips: ÂèØ‰ª•Â∞ÜÂÖ∂ËÆæÁΩÆ‰∏∫ `()=>[]` ‰ªéËÄå‰∏ç‰∏ªÂä®ÊâßË°å getter(), ÂèØ‰ª•ÈÄöËøáË∞ÉÁî®ËøîÂõûÁöÑ reLoad Êù•Ë∞ÉÁî® getter()
   */
  deps?: WatchSource<any>;
  /** data ÁöÑÈªòËÆ§ÂÄº */
  defaultData?: T;
  getter: () => Promise<T>;
  /** Â§ÑÁêÜÊï∞ÊçÆÊòØÂê¶Ë¶ÅÂíå‰πãÂâçÁöÑÊï∞ÊçÆËøõË°åÂêàÂπ∂ */
  dataMergeFun?: (oldData: T, newData: T) => T;
}
export function usePromiseComputed<T, Err = Error>({
  deps,
  getter,
  dataMergeFun = (oldData, newData) => newData,
  defaultData,
}: usePromiseComputedOptions<T>) {
  const r = new PromiseObj<T, Err>();
  if (defaultData !== undefined) {
    r.data = defaultData;
  }
  return customRef<PromiseObj<T, Err>>((track, trigger) => {
    if (!deps) {
      watchEffect(() => update(getter()));
    } else if (deps) {
      if (deps instanceof Function) {
        const depsSource = deps();
        if (Array.isArray(depsSource) && depsSource.length === 0) {
          // ÁâπÊÄßÔºå‰º†ÈÄíÁ©∫Êï∞ÁªÑÂàôÂÖà‰∏çÊâßË°å getter ÔºåÁî±Áî®Êà∑Ëá™Â∑±ÂÜ≥ÂÆöÊó∂Êú∫ÊâßË°å reload
        } else {
          watch(deps, () => update(getter()), { immediate: true });
        }
      } else {
        watch(deps, () => update(getter()), { immediate: true });
      }
    }
    function update(p: Promise<T>) {
      r.setP(p);
      r.pending = true;
      r.fulfilled = false;
      r.rejected = false;

      // Á´ãÂç≥Ëß¶Âèë‰ºöÂØºËá¥Ê≠ªÂæ™ÁéØÔºåÊâÄ‰ª•ÂåÖË£π‰∏ÄÂ±Ç
      nextTick(trigger);
      p.then((res) => {
        // ÈÅøÂÖç „ÄåÊ±ÇÂÄºfun„Äç Á¨¨‰∏ÄÊ¨°ÊâßË°å‰∫ßÁîüÁöÑ promise ÊØî Á¨¨‰∫åÊ¨°‰∫ßÁîüÁöÑÂêéÁªìÊùü ÂØºËá¥ Êï∞ÊçÆÈîôËØØÁöÑÈááÁî®‰∫ÜÁ¨¨‰∏ÄÊ¨°ÁöÑ
        if (r.equalP(p)) {
          r.pending = false;
          r.fulfilled = true;
          r.data = dataMergeFun(r.data, res);
        }
      })
        .catch((e) => {
          if (r.equalP(p)) {
            r.pending = false;
            r.rejected = true;
            r.error = e;
          }
        })
        .finally(() => {
          if (r.equalP(p)) {
            trigger();
          }
        });
    }
    r.reLoad = () => {
      update(getter());
    };
    return {
      get() {
        track();
        return r;
      },
      set(newValue) {
        console.warn("‰∏çÂèØËÆæÁΩÆÂÄº");
      },
    };
  });
}

/** Èò≤ÊäñÁöÑ ref */
export function useDebouncedRef<T>(value: T, delay = 200) {
  let timeout = 0;
  return customRef<T>((track, trigger) => {
    return {
      get() {
        track();
        return value;
      },
      set(newValue) {
        clearTimeout(timeout);
        timeout = setTimeout(() => {
          value = newValue;
          trigger();
        }, delay) as unknown as number;
      },
    };
  });
}
